import * as React from "react";
import intl from "react-intl-universal";
import { Link } from "react-router-dom";
import Image from "components/Image";
import Icon from "components/Icon";
import logoMain from "images/logo-main.png";
import arFlag from "images/arabic.png";
import enFlag from "images/english.png";
import withSessionContext from "HOC/withSessionContext";
import MenuBar from "components/MenuBar";
import "./Navbar.css";

class Navbar extends React.Component {
  state = {
    active: ""
  };

  //todo: remove
  componentDidMount() {
    this.props.sessionContext._setState({
      roles: [
        "RBC.BC01.ENTRY",
        "RBC.BC01.EXIT",
        "RBC.BC01.CHANGE.XDATE",
        "RBC.BC01.ALLOW.ENTER.FLIGHT",
        "RBC.SKIP.BIOMETRICS.VERIFY",
        "RBC.BC01.ALLOW.UNKNOWN.AIRLINE",
        "RBC.CROSS.WITH.SERIOUS.CWL.HIT",
        "RBC.CLEAR.MISC.ID",
        "RBC.CLEAR.MISC.ID.DENY"
      ]
    });
  }
  /////////////////////////////////////////////

  setActive = nav => {
    this.setState(state => {
      return {
        active: nav === state.active ? "" : nav
      };
    });
  };

  signout = () => {
    this.setState(
      {
        active: ""
      },
      () => this.props.sessionContext.signout()
    );
  };

  toggleLocale = () => {
    this.setState(
      {
        active: ""
      },
      () => this.props.sessionContext.changeLocale()
    );
  };

  render() {
    const { active } = this.state;
    const { locale, userId } = this.props.sessionContext;
    return (
      <nav className="nav">
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <div style={{ display: "flex", alignItems: "flex-end" }}>
            <Link to="/authenticated/home" className="navbar-brand">
              <Image
                src={logoMain}
                alt="brand logo"
                style={{ height: "80px", width: "80px" }}
              />
            </Link>
            <div>
              <div style={{ color: "white" }}>
                <Icon icon="fa fa-map-marker" />
                &nbsp;Riyadh, Saudi Arabia
              </div>
              <MenuBar
                active={active}
                setActive={this.setActive}
                locale={locale}
              />
            </div>
          </div>
          <div className="navbar-right">
            <div style={{ color: "white" }} className="date-time">
              <Icon icon="fa fa-calendar" />
              &nbsp;
              {new Date().toLocaleDateString()}
            </div>
            <div
              className={
                "dropdown" +
                (locale === "en" ? " is-right" : "") +
                (active === "user" ? " is-active" : "")
              }
            >
              <div
                className="dropdown-trigger"
                onClick={() => this.setActive("user")}
              >
                <a className="button">
                  <Icon className="is-large" icon="fa fa-user-circle fa-2x" />
                  &nbsp;&nbsp;
                  <span>{userId}</span>
                  <Icon icon="fa fa-angle-down" />
                </a>
              </div>
              <div className="dropdown-menu" role="menu">
                <div className="dropdown-content">
                  <span className="dropdown-item">
                    <a>
                      <Icon
                        style={{ color: "white" }}
                        icon="fa fa-question-circle"
                      />
                      &nbsp;&nbsp;
                      <span style={{ color: "white", fontWeight: "600" }}>
                        {intl.get("help")}
                      </span>
                    </a>
                  </span>
                  <span className="dropdown-item">
                    <a onClick={this.signout}>
                      <Icon style={{ color: "white" }} icon="fa fa-power-off" />
                      &nbsp;&nbsp;
                      <span style={{ color: "white", fontWeight: "600" }}>
                        {intl.get("signout")}
                      </span>
                    </a>
                  </span>
                </div>
              </div>
            </div>

            <div
              className={
                "dropdown" +
                (locale === "en" ? " is-right" : "") +
                (active === "locale" ? " is-active" : "")
              }
            >
              <div
                className="dropdown-trigger"
                onClick={() => this.setActive("locale")}
              >
                <a className="button">
                  <Image
                    src={locale === "en" ? enFlag : arFlag}
                    style={{ cursor: "pointer" }}
                  />
                  <Icon icon="fa fa-angle-down" />
                </a>
              </div>
              <div className="dropdown-menu" role="menu">
                <div className="dropdown-content">
                  <span className="dropdown-item">
                    <a
                      onClick={this.toggleLocale}
                      style={{ display: "flex", alignItems: "center" }}
                    >
                      <Image src={locale === "en" ? arFlag : enFlag} />{" "}
                      &nbsp;&nbsp;
                      <span style={{ color: "white", fontWeight: "600" }}>
                        {locale === "en" ? "Arabic" : intl.get("english")}
                      </span>
                    </a>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </nav>
    );
  }
}

export default withSessionContext(Navbar);



